import { defineStore } from "pinia";
import axios from "axios";
import { config } from "./config";
import Cookies from "js-cookie";

export const usersStore = defineStore("users", {
  state: () => ({
    login: "",
  }),

  actions: {
    setCookies(responseData: any) {
      Cookies.set("access_token", responseData.access_token);
      Cookies.set("token_expiration", responseData.expires_in);
    },
    setLogin(login: string) {
      this.login = login;
      Cookies.set("login", login);
    },
    clearCookies() {
      Cookies.remove("access_token");
      Cookies.remove("token_expiration");
      Cookies.remove("login");
    },
    initializeLogin() {
      const savedLogin = Cookies.get("login");
      if (savedLogin) {
        this.login = savedLogin;
      }
    },

    async logout() {
      this.clearCookies();
      this.login = "";
    },

    async createUser(regData: any) {
      try {
        const response = await axios.post(config.backendURL + "/auth/signup", {
          login: regData.login,
          password: regData.password,
        });

        if (response.status === 201) {
          this.setCookies(response.data);
          this.setLogin(regData.login);
        }
      } catch (error) {
        console.error("Произошла ошибка при регистрации:", error);
      }
    },

    async loginUser(loginData: any) {
      try {
        const response = await axios.post(config.backendURL + "/auth/login", {
          login: loginData.login,
          password: loginData.password,
        });

        if (response.status === 200) {
          this.setCookies(response.data);
          this.setLogin(loginData.login);
        }
      } catch (error) {
        console.error("Произошла ошибка при авторизации:", error);
      }
    },
  },

  getters: {},
});
